/************** CategoriesList **************/

#categoriesListPage {
    width: 100%;
    min-width: 320px;
    max-width: var(--max-width);
    margin: auto;
    transition: bottom 300ms;
  }
  #categoriesListPageHeader {
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
    background-color: var(--main-color);
  }
  #categoriesListPageBack {
    margin-left: 20px;
    width: 35px;
    height: 35px;
    fill: white;
    cursor: pointer;
  }
  #categoriesListPageBack:hover {
    fill: black;
  }
  #categoriesListPageHeaderTitle {
    color: white;
    margin: 0;
    margin-right: 30px;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
  }
  #categoriesListBody {
    list-style: none;
    padding: 0;
    margin-top: 5px;
    width: 100%;
    max-width: var(--max-width);
    min-width: 320px;
  }
  #categoriesListBody li {
    position: relative;
    background-color: var(--background-white);
    font-size: calc(17px + 6 * ((var(--vw) - 320px) / 680));
    cursor: pointer;
    margin-bottom: 5px;
    padding: 10px;
    padding-right: 45px;
    border-radius: 10px;
    margin-left: 10px;
    margin-right: 10px;
    color: var(--text-kinda-black);
    transition: transform 200ms;
  }
  #categoriesListBody li:hover {
    background-color: var(--main-color);
    color: white;
  }
  #categoriesListBody li:hover::before {
    background: var(--second-color);
    background: radial-gradient(circle, var(--second-color) 25%, var(--background-white) 30%);
  }
  #categoriesListBody li::before {
    content: " ";
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    height: 16px;
    width: 16px;
    background: var(--second-color);
    background: radial-gradient(circle, var(--background-white) 25%, var(--second-color) 30%);
    border-radius: 50%;
  }
  @media (max-width: 450px) {
    #categoriesListPageBack {
      width: 30px;
      height: 30px;
    }
  }
  
  